<template>
  <div class="home position-relative d-flex justify-content-center align-items-center">
    <div>
      <div class="d-flex justify-content-center align-items-center">
        <canvas ref="sketchpad" class="position-absolute" id="myCanvas" height="280" width="280"  @touchstart.prevent="touchStart($event)" @touchmove.prevent="eventMove($event)" @mousedown="touchStart($event)" @mousemove="eventMove($event)">
        </canvas>
        <div class="position-absolute" v-if="!drawing" @click="drawing = true">
          <img class="" src="../assets/img/patronage_thanks.png" height="60" width="60" alt="">
          <p class="text-white">let's scratch</p>
        </div>
        <img id="img1" class="" src="../assets/img/patronage_thanks.png" height="280" width="280" alt="">
      </div>
      <p :style="{opacity: opacity}" class="h8 text-danger" >congratilation!!</p>
    </div>
  </div>
</template>

<script>
let canvas
let ctx
export default {
  name: 'home',
  data () {
    return {
      drawing: false,
      opacity: 0
    }
  },
  mounted () {
    this.init()
  },
  created () {
  },
  computed: {
  },
  methods: {
    init () {
      canvas = document.querySelector('#myCanvas')
      ctx = canvas.getContext('2d')
      ctx.beginPath()
      const gradient = ctx.createLinearGradient(50, 50, 660, 680)
      gradient.addColorStop(0, 'gray')
      gradient.addColorStop(0.25, 'gray')
      gradient.addColorStop(0.5, 'white')
      gradient.addColorStop(0.75, 'white')
      gradient.addColorStop(1, 'white')
      ctx.fillStyle = gradient // 刮刮乐图层的填充色
      ctx.lineCap = 'round' // 绘制的线结束时为圆形
      ctx.lineJoin = 'round' // 当两条线交汇时创建圆形边角
      ctx.lineWidth = 15 // 单次刮开面积
      ctx.fillRect(0, 0, 280, 280)
      ctx.save()// 保存画布上面的状态
      ctx.closePath()
      ctx.globalCompositeOperation = 'destination-out'
      canvas.style.display = 'inherit'
    },
    touchStart (e) {
      this.drawing = true
      e = e || window.event
      if (typeof e.touches !== 'undefined') {
        e = e.touches[0]
      }
      var x = e.pageX - e.target.offsetLeft
      var y = e.pageY - e.target.offsetTop
      ctx.moveTo(x, y) // 定位開始點
      this.eventMove(e)
    },
    eventMove (e) {
      if (!this.drawing) return
      e = e || window.event
      if (typeof e.touches !== 'undefined') {
        e = e.touches[0]
      }
      var x = e.pageX - e.target.offsetLeft
      var y = e.pageY - e.target.offsetTop
      ctx.lineTo(x, y) // 移動並結束點
      ctx.stroke()
      this.clear()
    },
    clear () {
      let data = ctx.getImageData(0, 0, 280, 280).data// 得到canvas的全部数据
      let half = 0
      // length = canvasWidth * canvasHeight * 4，一个像素块是一个对象rgba四个值，a范围为0~255
      for (var i = 3, length = data.length; i < length; i += 4) { // 因为有rgba四个值，下标0开始，所以初始i=3
        data[i] === 0 && half++// 存在imageData对象时half加1  PS:该像素区域透明即为不存在该对象
      }
      // 当刮开的区域大于等于60%时，则可以开始处理结果
      if (half >= 280 * 280 * 0.6) {
        ctx.clearRect(0, 0, 280, 280)// 清空画布
        this.opacity = 1
      } else if (half >= 280 * 280 * 0.5) {
        this.opacity = 0.8
      } else if (half >= 280 * 280 * 0.4) {
        this.opacity = 0.5
      } else if (half >= 280 * 280 * 0.2) {
        this.opacity = 0.2
      }
    }
  }
}
</script>

<style>
.home {
  height: 100vh;
}
</style>



